const inBtn = document.getElementsByClassName("in")[0];
const outBtn = document.getElementsByClassName("out")[0];
const textDiv = document.getElementsByClassName("inOutText")[0];
const loading = document.getElementsByClassName("loading")[0];
inBtn.onclick = ()=>{
  console.log("in");
  outBtn.className = "out";
  inBtn.className = "in choose";
  textDiv.innerHTML = "入校码"
  load();
}
outBtn.onclick = ()=>{
  console.log("out");
  outBtn.className = "out choose";
  inBtn.className = "in";
  textDiv.innerHTML = "出校码"
  load();
}
const load = ()=>{
  loading.className = "onLoading";
  setTimeout(()=>{
  loading.className = "";
},300);
}

